import {graphql} from 'gatsby';
import * as React from 'react';

import BlogSummary from '../components/blog/summary';
import Layout from '../components/layout';
import SEO from '../components/seo';

import {Unnamed_3_Query} from '../graphqlTypes';

interface Props {
  data: Unnamed_3_Query;
}

const IndexPage = ({data: {allMarkdownRemark}}: Props): React.ReactElement => (
  <Layout>
    <SEO />
    {allMarkdownRemark!.edges.map(({node: blog}) => (
      <BlogSummary key={blog.fields!.slug!} blog={blog} />
    ))}
  </Layout>
);

export default IndexPage;

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      edges {
        node {
          fields {
            slug
          }
          ...BlogSummary
        }
      }
    }
  }
`;
